import React, { useEffect } from "react";
declare global {
  interface Window {
    Kakao: any;
  }
}
const KakaoShareButton = () => {
  useEffect(() => {
    createKakaoButton();
  }, []);

const url:string = window.location.href;

  const createKakaoButton = () => {
    // kakao sdk script   window.Kakao  
    if (window.Kakao) {
      const kakao = window.Kakao;
      //  initialization 
      if (!kakao.isInitialized()) {
        //  step   javascript key   initialize
        kakao.init(process.env.REACT_APP_KAKAO_KEY);
      }
      kakao.Link.createDefaultButton({
        // Render  id=kakao-link-btn     
        container: "#kakao-link-btn",
        objectType: "feed",
        content: {
          title: "ai    ",
          description: "#mood #ootd",
          imageUrl: url, // i.e. process.env.FETCH_URL + '/logo.png'
          link: {
            mobileWebUrl: url,
            webUrl: url,
          },
        },
        social: {
          likeCount: 77,
          commentCount: 55,
          sharedCount: 333,
        },
        buttons: [
          {
            title: "ai    !",
            link: {
              mobileWebUrl: url,
              webUrl: url,
            },
          },
        ],
      });
    }
  };
  return (
    
      <button id="kakao-link-btn" className="share-btn">
        <img src="images/kakao-talk.png" alt="kakao-share-icon" />
      </button>
  );
};
export default KakaoShareButton;
