import { useState } from "react";
import axios from "axios";
import ReactLoading from "react-loading";
import { Link } from "react-router-dom";
import Social from "../components/Social";

const Upload = ({ match }: any) => {
  //https://54.67.69.32:443/
  //http://54.67.69.32:80/
  const url: string = "http://54.67.69.32:80/";
  const [file, setFile] = useState("");
  const [fileName, setFileName] = useState("");
  const [inputData, setInputData] = useState({
    id: Date.now(),
    gender: "",
    age: "",
    style: "",
  });
  const { id, gender, age, style } = inputData;
  let [aiData, setAiData] = useState([
    {
      id: "",
      mood1: "",
      mood2: "",
      mood3: "",
      mood4: "",
      mood5: "",
      mood6: "",
      mood7: "",
      mood8: "",
    },
  ]);

  const [isShown, setIsShow] = useState(false);
  const [loading, setLoading] = useState(false);
  const [imageSrc, setImageSrc] = useState("");
  const [resData, setResData] = useState(false);

  //  ,  
  const onLoadFile = (e: any) => {
    const file = e.target.files[0];
    const fileName = e.target.files[0].name;
    setFile(file);
    setFileName(fileName);
    setImageSrc(URL.createObjectURL(file));
  };

  //  
  const onInputChange = (e: any) => {
    const { name, value } = e.target;
    setInputData({
      ...inputData,
      [name]: value,
    });
  };

  //  
  const handleSubmit = (e: any) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("images", file);
    formData.append("fileName", fileName);
    formData.append("data", JSON.stringify(inputData));
    console.log(JSON.stringify(inputData));
    setLoading(true); //  true

    //  axios 
    try {
      axios
        .post(url, formData)
        .then((res) => {
          setLoading(false); //  false
          console.log("res", res);
          console.log(res.data.mood[0]["1-"]);
          aiData = [
            {
              id: res.data.id,
              mood1: res.data.mood[0]["1-"],
              mood2: res.data.mood[0]["2-"],
              mood3: res.data.mood[0]["3-"],
              mood4: res.data.mood[0]["4-"],
              mood5: res.data.mood[0]["5-"],
              mood6: res.data.mood[0]["6-"],
              mood7: res.data.mood[0]["7-"],
              mood8: res.data.mood[0]["8-"],
            },
          ];
          setAiData([...aiData]);
          console.log("aiData", typeof aiData, aiData);
          setIsShow(true); //   false, ( true)
          setResData(true); //  true
        })
        .catch(function (error) {
          setLoading(false);
          if (error.response) {
            alert("      .");
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
          } else if (error.request) {
            alert("      .");
            console.log(error.request);
          } else {
            alert(
              "        ."
            );
            console.log("Error", error.message);
          }
          console.log(error.config);
        });
    } catch (e) {
      console.log(e);
      alert(
        "     .      "
      );
    }
  };

  if (loading) {
    return (
      <div>
        <div>
          <h2>ai </h2>
          <h2> !</h2>
          <div className="spinner">
            <ReactLoading
              type="spin"
              color="fff"
              height={"30%"}
              width={"30%"}
            />
          </div>
          {imageSrc && (
            <img className="preview" src={imageSrc} alt="preview-img" />
          )}
        </div>
      </div>
    );
  }

  if (resData) {
    return (
      <div>
        <h1> motd ..!</h1>
        {imageSrc && (
          <img className="preview" src={imageSrc} alt="preview-img" />
        )}
        <div>
          {aiData &&
            aiData.map((item) => (
              <div key={item.id}>
                {item.mood1[1]}, {item.mood1[0]}%
                <br />
                {item.mood2[1]}, {item.mood2[0]}%
                <br />
                {item.mood3[1]}, {item.mood3[0]}%
                <br />
                {item.mood4[1]}, {item.mood4[0]}%
                <br />
                {item.mood5[1]}, {item.mood5[0]}%
                <br />
                {item.mood6[1]}, {item.mood6[0]}%
                <br />
                {item.mood7[1]}, {item.mood7[0]}%
                <br />
                {item.mood8[1]}, {item.mood8[0]}%
              </div>
            ))}
            <p>
              #motd #mood #ootd # #
            </p>
        </div>

        <Social />
        <div>
          <Link to="/" className="button text-link">
             
          </Link>
        </div>
      </div>
    );
  }

  return (
    <>
      <div className="upload" hidden={isShown}>
        <h1> </h1>
        <div className="contents">
          <h4>
              !   . 
               
          </h4>

          <form onSubmit={handleSubmit} encType="multipart/formdata">
            <div className="input">
              <div className="input-gender">
                <h3></h3>
                <label htmlFor="female">
                  <input
                    type="radio"
                    name="gender"
                    id="female"
                    value=""
                    onChange={onInputChange}
                    required
                  />
                  <span></span>
                </label>
                <label htmlFor="male">
                  <input
                    type="radio"
                    id="male"
                    name="gender"
                    value=""
                    onChange={onInputChange}
                  />
                  <span></span>
                </label>
              </div>
              <div className="input-age">
                <h3></h3>
                <label htmlFor="age0">
                  <input
                    type="radio"
                    id="age0"
                    name="age"
                    value="10"
                    onChange={onInputChange}
                    required
                  />
                  <span>10 </span>
                </label>
                <label htmlFor="age10">
                  <input
                    type="radio"
                    id="age10"
                    name="age"
                    value="10"
                    onChange={onInputChange}
                  />
                  <span>10</span>
                </label>
                <label htmlFor="age20">
                  <input
                    type="radio"
                    id="age20"
                    name="age"
                    value="20"
                    onChange={onInputChange}
                  />
                  <span>20</span>
                </label>
                <label htmlFor="age30">
                  <input
                    type="radio"
                    id="age30"
                    name="age"
                    value="30"
                    onChange={onInputChange}
                  />
                  <span>30</span>
                </label>
                <label htmlFor="age40">
                  <input
                    type="radio"
                    id="age40"
                    name="age"
                    value="40"
                    onChange={onInputChange}
                  />
                  <span>40</span>
                </label>
                <label htmlFor="age50">
                  <input
                    type="radio"
                    id="age50"
                    name="age"
                    value="50"
                    onChange={onInputChange}
                  />
                  <span>50</span>
                </label>
                <label htmlFor="age60">
                  <input
                    type="radio"
                    id="age60"
                    name="age"
                    value="60"
                    onChange={onInputChange}
                  />
                  <span>60 </span>
                </label>
              </div>
              <div className="input-file">
                <h3> </h3>
                <input
                  id="file"
                  type="file"
                  name="file"
                  required
                  onChange={onLoadFile}
                />
              </div>
            </div>
            {imageSrc && (
              <img className="preview" src={imageSrc} alt="preview-img" />
            )}
            <div>
              <button type="submit" className="button">
                ai  
              </button>
            </div>
          </form>
        </div>
      </div>
      {/* <div hidden={!isShown}></div> */}
    </>
  );
};

export default Upload;
